<template>
	<div id="JobInfo">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">招聘详情</div>
      </div>
    </div>
    <div class="login_box">
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <img v-if="JobInfo.user" :src="JobInfo.user.userimg||'static/img/img_tx_zp.png'" width="20px" style="vertical-align: bottom"/>
        <span v-if="JobInfo.user" class="font_12 color_c10804">{{JobInfo.user.username}}</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="width: 100%;padding-right: 10px;">
          {{JobInfo.title}}
          <span class="float_right font_12 color_8b8b8b">发布时间:{{JobInfo.addtime}}</span>
        </span>
        <div class="font_12 color_8b8b8b margin_top_10">{{JobInfo.price}}</div>
      </div>
      <!--<div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">-->
        <!--<span class="font_12 margin_left_5 color_ffffff background_color_c10804" style="padding: 2px 10px;">{{JobInfo.label}}</span>-->
        <!--&lt;!&ndash;<span class="font_12 margin_left_5 color_40a3f3 background_color_d2eefe" style="padding: 2px 10px;">大专</span>&ndash;&gt;-->
        <!--&lt;!&ndash;<span class="font_12 margin_left_5 color_40a3f3 background_color_d2eefe" style="padding: 2px 10px;">1年以上工作经验</span>&ndash;&gt;-->
      <!--</div>-->
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="text-align: left;display: inline-block;width: 20%;padding-right: 10px;">工作地址</span>
        <span class="font_14 color_8b8b8b">{{JobInfo.address}}</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="text-align: left;display: inline-block;width: 20%;padding-right: 10px;">年限要求</span>
        <span class="font_14 color_8b8b8b">{{JobInfo.age}}</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="text-align: left;display: inline-block;width: 20%;padding-right: 10px;">学历要求</span>
        <span class="font_14 color_8b8b8b">{{JobInfo.xueli}}</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="text-align: left;display: inline-block;width: 20%;padding-right: 10px;">要求描述</span>
        <span class="font_14 color_8b8b8b" style="display: inline-block;width: 75%;vertical-align: text-top;">{{JobInfo.content}}</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="text-align: left;display: inline-block;width: 20%;padding-right: 10px;">福利待遇</span>
        <span class="font_14 color_8b8b8b">{{JobInfo.daiyu}}</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="text-align: left;display: inline-block;width: 20%;padding-right: 10px;">联系人</span>
        <span class="font_14 color_8b8b8b" style="display: inline-block;width: 75%;">{{JobInfo.uname}}</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom" style="margin: 0 5px">
        <span class="font_14 text_center" style="text-align: left;display: inline-block;width: 20%;padding-right: 10px;">联系电话</span>
        <span class="font_14 color_8b8b8b" style="display: inline-block;width: 75%;">{{JobInfo.tell}}</span>
      </div>
      <div class="login_bar background_color_ffffff login_top" style="margin-top: 40px">
        <img :src="item" class="img_service" v-for="(item,index) in JobInfo.sameimg.split(',')" @click="imgpreview(index)"/>
      </div>
    </div>
	</div>
</template>
<script>
  import { ImagePreview } from 'vant';
	export default {
		name: 'JobInfo',
		data() {
			return {
        userId:localStorage.getItem('userId'),
        samecityId:'',
        JobInfo:[],
        imgList:[],
			}
		},
    mounted() {
      this.samecityId = this.$store.state.JobInfo.samecityid;
      this.getJobInfo();
    },
		methods:{
      imgpreview(index){
        ImagePreview(
          {
            images:this.JobInfo.sameimg.split(','),
            showIndicators:true,
            startPosition:index,
            showIndex:true,
          }
        );
      },
      getJobInfo(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          samecityid:$this.samecityId,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecityinfo", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.JobInfo = rpdata.data.data;
            if($this.JobInfo.sameimg){
              $this.imgList = $this.JobInfo.sameimg.split(',');
            }
          }
        })
        .catch(function(err) {})
      }
		}
	}
</script>
<style scoped>
  .header {
    box-shadow: 0px 2px 10px lightgrey;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 99;
  }
  .img_service{
    width: 32.3%;
    padding: 1px;
  }
  .login_bottom{
    border-bottom: 1px solid #eeeeee;
  }
  .login_input{
    margin-left: 8%;
    border: 0;
  }
  .login_bar{
    padding: 15px 15px;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



